.about-container {
   max-width: 800px;
   margin: 0 auto;
   padding: 20px;
 }
 
 .about-us {
   font-size: 36px;
   font-weight: bold;
   color: black;
   margin: 81px 0 40px 0;
 }
 
 .about-us__dscr {
   font-size: 18px;
   line-height: 1.5;
   color: #9C9EA3;
 }
 
 @media screen and (max-width: 767px) {
   .about-us {
     font-size: 28px;
     margin-bottom: 10px;
   }
 
   .about-us__dscr {
     font-size: 16px;
   }
 }
 
 @media screen and (max-width: 480px) {
   .about-us {
     font-size: 24px;
   }
 
   .about-us__dscr {
     font-size: 14px;
   }
 }
